import {ThunkDispatch} from "redux-thunk";
import {AppRootStateType} from "./Store";
import axios from "axios";

export type RequestStatusType = 'loading' | 'succeeded' | 'failed'


export interface stateProps {
    nameValue: string,
    emailValue: string,
    messageValue: string,
    isBtnDisabled: boolean,
    status: RequestStatusType,
    isSuccessModal: boolean,

}

const initialState: stateProps = {
    nameValue: '',
    emailValue: '',
    messageValue: '',
    isBtnDisabled: true,
    status: 'succeeded',
    isSuccessModal: false
}

interface Action<T> {
    type: ActionType,
    payload: T
}

export enum ActionType {
    ON_SET_NAME_VALUE = "NS/CONTACTS/ON_SET_NAME_VALUE",
    ON_SET_EMAIL_VALUE = "NS/CONTACTS/ON_SET_EMAIL_VALUE",
    ON_SET_MESSAGE_VALUE = "NS/CONTACTS/ON_SET_MESSAGE_VALUE",
    ON_SET_MAIN_BTN_DISABLED = "NS/CONTACTS/ON_SET_MAIN_BTN_DISABLED",
    ON_CHANGE_STATUS = "NS/CONTACTS/ON_SET_MAIN_BTN_DISABLED",
    ON_CHANGE_SUCCESS_MODAL =  "NS/CONTACTS/ON_CHANGE_SUCCESS_MODAL",
}


//ActionCreator

export const onSetNameValue = (nameValue: string): Action<string> => ({
    type: ActionType.ON_SET_NAME_VALUE,
    payload: nameValue
});
export const onSetEmailValue = (emailValue: string): Action<string> => ({
    type: ActionType.ON_SET_NAME_VALUE,
    payload: emailValue
});
export const onSetMessageValue = (messageValue: string): Action<string> => ({
    type: ActionType.ON_SET_NAME_VALUE,
    payload: messageValue
});
export const onSetIsDisabled = (isDisabled: boolean): Action<boolean> => ({
    type: ActionType.ON_SET_MAIN_BTN_DISABLED,
    payload: isDisabled
})
export const onSetStatus = (status: RequestStatusType): Action<RequestStatusType> => ({
    type: ActionType.ON_CHANGE_STATUS,
    payload: status
})

export const onSetIsSuccessModal = (isSuccess : boolean) : Action<boolean> => ({
    type : ActionType.ON_CHANGE_SUCCESS_MODAL,
    payload: isSuccess
})

export const putData = (name: string, email: string, message: string) =>
    (dispatch: ThunkDispatch<AppRootStateType, {}, ActionsCreatorType>) => {
        dispatch(onSetStatus('loading'))
        dispatch(onSetIsDisabled(true))
        axios.post("https://stpm-nodejs-server.herokuapp.com/message", {
            name, email, message
        }).then(res => {
            dispatch(onSetNameValue(res.data.name))
            dispatch(onSetEmailValue(res.data.email))
            dispatch(onSetMessageValue(res.data.message))
            dispatch(onSetIsSuccessModal(true))
            dispatch(onSetStatus('succeeded'))

        })


    }


const ContactsReducer = (state: stateProps = initialState, action: Action<string & boolean & RequestStatusType>): stateProps => {
    switch (action.type) {
        case ActionType.ON_SET_EMAIL_VALUE:
            return {...state, emailValue: action.payload};
        case ActionType.ON_SET_NAME_VALUE:
            return {...state, nameValue: action.payload};
        case ActionType.ON_SET_MESSAGE_VALUE:
            return {...state, messageValue: action.payload};
        case ActionType.ON_SET_MAIN_BTN_DISABLED:
            return {...state, isBtnDisabled: action.payload};
        case ActionType.ON_CHANGE_STATUS:
            return {...state, status: action.payload};
        case ActionType.ON_CHANGE_SUCCESS_MODAL: {
            return  {...state, isSuccessModal: action.payload}
        }

    }

    return state
}


type ActionsCreatorType = ReturnType<typeof onSetNameValue> |
    ReturnType<typeof onSetEmailValue> | ReturnType<typeof onSetMessageValue> |
    ReturnType<typeof onSetIsDisabled> | ReturnType<typeof onSetStatus>

export default ContactsReducer